*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

img {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

body, html {
  text-align: center;
  -ms-flex-align: center;
  align-items: center;
  justify-items: center;
  width: 100%;
  height: 100%;
  font-family: Roboto, sans-serif;
  display: grid;
}

.container {
  width: 95%;
  max-width: 350px;
  margin: 0 auto;
  padding: 20px;
}

.container .header {
  text-align: left;
}

.container .header h1 {
  margin-top: 30px;
}

.container form .form-group {
  margin-top: 20px;
  margin-bottom: 20px;
  position: relative;
}

.container form .inputField {
  box-shadow: none;
  border: 2px solid #1da1f2;
  border-radius: 4px;
  outline: none;
  width: 100%;
  height: 55px;
  padding: 10px 10px 0;
  font-size: 14px;
}

.container form label {
  transition: all .3s;
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translate(0, -50%);
}

.container form .inputField:focus + label, .container form .inputField:valid + label {
  transform: translate(0, -125%);
}

button.submitButton {
  color: #fff;
  cursor: pointer;
  background: #1da1f2;
  border: 1px solid #1da1f2;
  border-radius: 30px;
  outline: none;
  width: 100%;
  height: 40px;
  font-weight: bold;
  transition: all .3s;
}

button.submitButton:hover {
  opacity: .7;
}

.footer {
  color: #1da1f2;
  margin-top: 30px;
}

.footer a {
  color: #1da1f2;
  text-decoration: none;
}

.footer span {
  padding: 10px;
}

